import React, { Component } from 'react';
import Charts from '../charts/charts';
import './charts.scss'
class Results extends Component {
    state={
        chartValues : {}
    }


    componentDidMount(){
        const resultsForChart = this.props.resultsForChart;
        let chartValues = {};
        let xAxis = [];
        let yAxis = [];
        resultsForChart.map((elem,i)=>{
            xAxis.push(i+1);
            yAxis.push(elem.answerTime);
        });
        chartValues.x = xAxis;
        chartValues.y = yAxis;
        this.setState({chartValues});
        console.log(chartValues, this.state.chartValues);
    }
    changeType=(event)=>{
        let chartType = event;
        this.setState({chartType});
    }
    render() {
        let chartValues = this.state.chartValues;
        console.log(chartValues);
        return (
            <div className="charts">
                <Charts 
                    chartValues={chartValues}/>
            </div>
        );
    }
}

export default Results;